import React, { useRef, useEffect, useState } from "react";
import Webcam from "react-webcam";
import "@tensorflow/tfjs-core";
import "@tensorflow/tfjs-backend-webgpu";
import * as poseDetection from "@tensorflow-models/pose-detection";
import * as tf from "@tensorflow/tfjs-core";

const PlankAnalyzer = () => {
  const webcamRef = useRef(null);
  const canvasRef = useRef(null);
  //    
  const [plankDuration, setPlankDuration] = useState(0);
  const previousLandmarks = useRef([]);

  useEffect(() => {
    const initializeTF = async () => {
      await tf.setBackend("webgpu");
      await tf.ready();
      console.log(" webgpu  ", tf.getBackend());
      const detectorConfig = { modelType: poseDetection.movenet.modelType.SINGLEPOSE_THUNDER };
      const detector = await poseDetection.createDetector(poseDetection.SupportedModels.MoveNet, detectorConfig);
      detectPose(detector);
    };

    const detectPose = async (detector) => {
      if (!webcamRef.current || !webcamRef.current.video) return;
      const video = webcamRef.current.video;
      const { videoWidth, videoHeight } = video;

      canvasRef.current.width = videoWidth;
      canvasRef.current.height = videoHeight;

      const ctx = canvasRef.current.getContext("2d");

      const runDetection = async () => {
        const poses = await detector.estimatePoses(video);
        if (poses.length > 0) {
          drawPose(poses[0], ctx);
        }
        requestAnimationFrame(runDetection);
      };

      requestAnimationFrame(runDetection);
    };

    const drawPose = (pose, ctx) => {
      ctx.clearRect(0, 0, canvasRef.current.width, canvasRef.current.height);
      const keypoints = pose.keypoints;
      const correctPosition = checkPlankPosition(keypoints);
      const movement = detectMovement(keypoints, previousLandmarks.current);

      if (correctPosition && !movement) {
        setPlankDuration((prevDuration) => prevDuration + 1);
      } else {
        setPlankDuration(0);
      }

      previousLandmarks.current = keypoints;

      //      .
      keypoints.forEach((keypoint) => {
        if (keypoint.score > 0.5) {
          const { x, y } = keypoint; //    x, y 
          ctx.beginPath();
          ctx.arc(x * canvasRef.current.width, y * canvasRef.current.height, 5, 0, 2 * Math.PI);
          ctx.fillStyle = "aqua"; //   
          ctx.fill();
        }
      });
    };

    initializeTF();
  }, []);

  const checkPlankPosition = (keypoints) => {
    const nose = keypoints.find((p) => p.name === "nose");
    const leftShoulder = keypoints.find((p) => p.name === "left_shoulder");
    const rightShoulder = keypoints.find((p) => p.name === "right_shoulder");
    const leftElbow = keypoints.find((p) => p.name === "left_elbow");
    const rightElbow = keypoints.find((p) => p.name === "right_elbow");
    const leftHip = keypoints.find((p) => p.name === "left_hip");
    const rightHip = keypoints.find((p) => p.name === "right_hip");
    const leftKnee = keypoints.find((p) => p.name === "left_knee");
    const rightKnee = keypoints.find((p) => p.name === "right_knee");
    const leftAnkle = keypoints.find((p) => p.name === "left_ankle");
    const rightAnkle = keypoints.find((p) => p.name === "right_ankle");

    // Check alignment
    const shouldersAligned = Math.abs(leftShoulder.y - rightShoulder.y) < 0.1;
    const hipsAligned = Math.abs(leftHip.y - rightHip.y) < 0.1;
    const kneesAligned = Math.abs(leftKnee.y - rightKnee.y) < 0.1;
    const anklesAligned = Math.abs(leftAnkle.y - rightAnkle.y) < 0.1;

    // Check shoulders and elbows
    const elbowsUnderShoulders =
      Math.abs(leftElbow.x - leftShoulder.x) < 0.1 && Math.abs(rightElbow.x - rightShoulder.x) < 0.1;

    return shouldersAligned && hipsAligned && kneesAligned && anklesAligned && elbowsUnderShoulders;
  };

  const detectMovement = (current, previous) => {
    if (!previous || previous.length === 0) return false;
    let totalMovement = 0;
    current.forEach((keypoint, index) => {
      const prevKey = previous[index];
      const movement = Math.sqrt((keypoint.x - prevKey.x) ** 2 + (keypoint.y - prevKey.y) ** 2);
      totalMovement += movement;
    });
    return totalMovement > 0.03;
  };

  return (
    <div className="Webcam" style={{ position: "relative", width: "100%", height: "auto" }}>
      <Webcam ref={webcamRef} style={{ width: "100%", height: "auto" }} />
      <canvas
        className="CanvasOverlay"
        ref={canvasRef}
        style={{ position: "absolute", top: 0, left: 0, width: "100%" }}
      />
      <div>Plank Duration: {plankDuration} seconds</div>
    </div>
  );
};

export default PlankAnalyzer;
